import React from "react";
import { AppBar, Toolbar } from "@mui/material";
import style from "./appbar.color.module.scss";
const AppBarCustom = ({ children, bigWidth = "60%", styles, screenWidth }) => {
  return (
    <React.Fragment>
      <AppBar
        color="colorPrimer"
        sx={{
          width: screenWidth > 1024 ? bigWidth : "100%",
          height: screenWidth > 1024 ? "100%" : "initial",
          ...styles,
        }}
      >
        <Toolbar
          className={style.container}
          variant="dense"
          sx={{
            minHeight: "2.3em !important",
            "& *": {
              color: "white !important",
            },

            flexDirection: screenWidth > 1024 ? "column" : "row-reverse",
            height: screenWidth > 1024 ? "100%" : "initial",
            paddingInline: screenWidth > 1024 ? "0 !important" : "initial",
          }}
        >
          {children}
        </Toolbar>
      </AppBar>
    </React.Fragment>
  );
};

export default AppBarCustom;
