import React from 'react';
import { GetCarouselImage } from '../Adapters/GetCarouselImage';

const useCallCarouselFetcher = () => {
  const [images, setCarouselImages] = React.useState();
  React.useEffect(() => {
    GetCarouselImage().then((response) => {
      setCarouselImages(response.data.Data);
    });
  }, []);

  return images;
};

export default useCallCarouselFetcher;
