import React, { useEffect, useState } from "react";

const useInfiniteCarouselSlide = (images, carouselRef) => {
  const [slideLocation, setSlideLocation] = useState(null);

  const listLength = images?.List[0]?.imageList?.length;
  function countdownTimer(startNumber, delay) {
    let counter = startNumber;
    const timer = setInterval(() => {
      if (carouselRef.current) {
        carouselRef.current.goTo(counter);
        counter--;
        if (counter < 0) {
          clearInterval(timer);
        }
      }
    }, delay);
  }

  useEffect(() => {
    const timeOut = setTimeout(() => {
      if (slideLocation === listLength - 1) {
        countdownTimer(listLength - 1, 9950);
      }
    }, 10000);

    return () => {
      clearTimeout(timeOut);
    };
  }, [slideLocation]);

  return [setSlideLocation,slideLocation];
};

export default useInfiniteCarouselSlide;
