import React, { useEffect } from 'react';
import { Link } from 'react-router-dom';
import styles from '../../../Styles/Menu.Options.module.scss';
import ComponentMemoizer from 'Global/Components/ComponentMemoizer/ComponentMemoizer';
import { sideBarLinks } from 'Routes/Protected/MainPage/shared/side.bar.links';
import { useAtom } from 'jotai';
const MenuLinksCreator = ({ Links, AccessRoles }) => {
  const [, setSideBarLinks] = useAtom(sideBarLinks);

  useEffect(() => {
    if (
      Links?.map(
        (option, i) =>
          AccessRoles?.map((role) =>
            role.toLowerCase()
          )?.includes(option.Route.toLowerCase()) ||
          AccessRoles?.map((role) =>
            role.toLowerCase().substring(1)
          )?.includes(option.Route.toLowerCase())
      )
    ) {
      const filtered_Routes = Links?.filter(
        (option, i) =>
          AccessRoles?.map((role) =>
            role.toLowerCase()
          )?.includes(option.Route.toLowerCase()) ||
          AccessRoles?.map((role) =>
            role.toLowerCase().substring(1)
          )?.includes(option.Route.toLowerCase())
      );

      setSideBarLinks(filtered_Routes);
    }
  }, [JSON.stringify(Links), JSON.stringify(AccessRoles)]);

  return (
    <React.Fragment>
      <ComponentMemoizer
        element={Links?.map(
          (option, i) =>
            (AccessRoles?.map((role) =>
              role.toLowerCase()
            )?.includes(option.Route.toLowerCase()) ||
              AccessRoles?.map((role) =>
                role.toLowerCase().substring(1)
              )?.includes(option.Route.toLowerCase())) && (
              <React.Fragment key={i}>
                <section className={styles.eachLink}>
                  <Link to={option.Route} replace>
                    <img
                      src={option.Picture}
                      alt={option.Title}
                      loading="..."
                      style={{
                        background: option.Picture,
                        cursor: 'pointer',
                        backgroundPosition: 'center center',
                        backgroundSize: 'cover',
                      }}
                      width={60}
                      height={60}
                    />
                  </Link>
                  <p style={{ fontSize: '0.8125em' }}>
                    {option.Title}
                  </p>
                </section>
              </React.Fragment>
            )
        )}
        dependecies={[localStorage.getItem('icons')]}
      />
    </React.Fragment>
  );
};

export default MenuLinksCreator;
