import React from 'react';
import useGetProfileImageOnMount from './Hooks/useGetProfileImageOnMount';
import UserInterface from './UserInterface';
import { useAtom } from 'jotai';
import { ImageAtom } from './ImageAtom';
import { env } from 'env';
import Cookies from 'js-cookie';
const MainPage = () => {
  const [RefreshForImage, setRefreshForImage] =
    useAtom(ImageAtom);
  useGetProfileImageOnMount(setRefreshForImage);

  return (
    <React.Fragment>
      {RefreshForImage ? (
        <UserInterface
          setRefreshForImage={setRefreshForImage}
        />
      ) : (
        <UserInterface
          setRefreshForImage={setRefreshForImage}
        />
      )}
    </React.Fragment>
  );
};

export default MainPage;
