import SwalAlert from 'Global/Components/CommonComponents/SwalAlert/SwalAlert';
import { Post } from 'Xhr/Xhr';
import { env } from 'env';

import { Base64Downloader } from 'utils/Base64Downloader';
export const GetUserManual = async (
  downloadingManualMsg
) => {
  SwalAlert('warning', downloadingManualMsg, false);
  return Post(
    'POST',
    `${process.env.REACT_APP_API_URL_API_CO_INFORMATION}`,
    false,
    {
      ReturnHelpFile: true,
    }
  ).then((response) => {
    if (response.data.resCode === 1) {
      Base64Downloader(
        response.data.Data,
        'manual',
        'data:application/pdf'
      );
    } else {
      SwalAlert('error', response.data.resMessage, false);
    }
  });
};
