import { GlobalContext } from 'Global/Context/GlobalContextProvider';
import Cookies from 'js-cookie';
import React, { useContext, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { useAtom } from 'jotai';
import { sideBarLinks } from '../shared/side.bar.links';

const useSetDefaultMenuSelection = (screenWidth) => {
  const [sidebarLinks] = useAtom(sideBarLinks);

  const userCookie = Cookies.get('user');

  const userAccessRole =
    userCookie && JSON.parse(userCookie)?.AccessRole;

  const navigate = useNavigate();

  console.log('sidebarLinks->', sidebarLinks);

  useEffect(() => {
    if (screenWidth > 1024) {
      if (
        !userAccessRole.includes('Patient') ||
        (userAccessRole.includes('Patient') &&
          userAccessRole?.length !== 1)
      ) {
        if (sidebarLinks) {
          navigate({
            pathname: `/main/${sidebarLinks[0]?.Route}`,
          });
        }
      }
    }
  }, [screenWidth, JSON.stringify(sidebarLinks)]);
};

export default useSetDefaultMenuSelection;
