import React, { useContext } from 'react';
import { Link } from 'react-router-dom';
import {
  Button,
  ListItemIcon,
  MenuItem,
  MenuList,
  Stack,
} from '@mui/material';
import { GlobalContext } from 'Global/Context/GlobalContextProvider';
const DrawerMenuLinkCreator = ({ Links, AccessRoles }) => {
  const navigate = useContext(GlobalContext)?.navigate;
  return (
    <React.Fragment>
      {Links?.map(
        (option, i) =>
          (AccessRoles?.map((role) =>
            role.toLowerCase()
          )?.includes(option.Route.toLowerCase()) ||
            option.isUnAuth) && (
            <React.Fragment key={i}>
              <span
                onClick={() => navigate(option.Route)}
                style={{
                  display: 'flex',
                  flexDirection: 'row-reverse',
                  alignItems: 'center',
                  gap: '.5em',
                  cursor: 'pointer',
                }}
              >
                {React.cloneElement(option.Picture, {
                  sx: {
                    fontSize: '1.5em !important',
                  },
                  color: 'colorPrimer',
                })}
                {option.Title}
              </span>
              {/* <Button
                color="colorPrimer"
                // sx={{
                //   border: "1px solid gainsboro",
                //   display: "flex",
                //   flexDirection: "row-reverse",
                //   justifyContent: "space-between",
                //   pl: "1.2em",
                // }}
                startIcon={}
              >
                {option.Title}
              </Button> */}
            </React.Fragment>
          )
      )}
    </React.Fragment>
  );
};

export default DrawerMenuLinkCreator;
