import React, { useEffect, useRef, useState } from 'react';
import Carousel from 'react-elastic-carousel';
import styles from '../../Styles/Image.Carousel.module.scss';
import { Stack, Container } from '@mui/material';
import { ArabicToPersian } from 'utils/ConvertToPersianTextFromArabic';
import useInfiniteCarouselSlide from '../../Hooks/useInfiniteCarouselSlide';
import NonCarousel from '../NonCarousel/NonCarousel';

const DesktopCarousel = ({ images }) => {
  const carouselRef = useRef(null);

  const [setSlideLocation, slideLocation] =
    useInfiniteCarouselSlide(images, carouselRef);

  // const images = useCallCarouselFetcher();
  const breakPoints = [
    { width: 1, itemsToShow: 1 },
    { width: 550, itemsToShow: 2, itemsToScroll: 2 },
    { width: 768, itemsToShow: 1 },
    { width: 1200, itemsToShow: 1 },
  ];
  const myArrow = () => <></>;

  return (
    <div className={styles.CarouselContainer}>
      <div className="carousel-wrapper">
        {images ? (
          <Stack>
            <Carousel
              ref={carouselRef}
              initialActiveIndex={0}
              breakPoints={breakPoints}
              renderArrow={myArrow}
              enableAutoPlay
              autoPlaySpeed={10000}
              onChange={(val, index) => {
                setSlideLocation(index);
              }}
            >
              {images?.List?.map((element) =>
                element?.imageList.map((imageList) => {
                  return (
                    <Stack position="relative">
                      <p
                        style={{
                          position: 'absolute',
                          bottom: 20,
                          right: 10,
                          backgroundColor: 'white',
                          width: '100%',
                          borderRadius: '0 10px 10px 10px',
                          paddingRight: '.2em',
                        }}
                      >
                        {ArabicToPersian(element?.Header)}
                      </p>
                      <img
                        style={{
                          width: '22.5em',
                          height: '13.75em',
                          objectFit: 'cover',
                          padding: 0,
                          margin: 0,
                        }}
                        src={imageList.Base64Image}
                        alt={imageList.Description}
                        onClick={() => {
                          window.open(
                            imageList?.ImageLink,
                            '_blank'
                          );
                        }}
                      />
                    </Stack>
                  );
                })
              )}
            </Carousel>
          </Stack>
        ) : (
          <></>
        )}

        <div></div>
      </div>
      <br />
      <br />
    </div>
  );
};

export default DesktopCarousel;
