import { Button, CssBaseline, Stack } from '@mui/material';
import React from 'react';
import { DrawerLinks } from '../variables/DrawerLinks';
import LogOutButton from './LogOutButton';
import ImageUploader from './ImageUploader';
import UserGuidFetchButton from './UserGuidFetchButton';
import { GlobalContext } from 'Global/Context/GlobalContextProvider';
import { useContext } from 'react';
import DrawerMenuLinkCreator from './DrawerMenuLinkCreator';
import FindLangKey from 'Global/Functions/FindLangKey/FindLangKey';
import {
  ContactEmergency,
  Info,
  Settings,
} from '@mui/icons-material';
import { themeColors } from 'Global/JsxThemeColor/JsxThemeColor';
const MainMenuDrawerOptions = ({
  setDrawerOpen,
  setRefreshInterface,
  setRefreshForImage,
}) => {
  const rootPaths = useContext(GlobalContext)?.rootPaths;
  const navigate = useContext(GlobalContext)?.navigate;
  const setSettingDrawerOpen =
    useContext(GlobalContext)?.setSettingDrawerOpen;
  const about_us = FindLangKey('general-public-about-us');
  const contact_us = FindLangKey(
    'general-public-contact-us'
  );

  return (
    <React.Fragment>
      <CssBaseline />
      <div
        style={{
          width: '100%',
          color: `${themeColors.ColorPrimary}`,
        }}
      >
        <Stack gap="1em">
          <ImageUploader
            setRefreshInterface={setRefreshInterface}
            setRefreshForImage={setRefreshForImage}
          />
          <DrawerMenuLinkCreator
            Links={DrawerLinks()}
            AccessRoles={rootPaths}
          />

          <span
            onClick={() => setSettingDrawerOpen(true)}
            style={{
              display: 'flex',
              flexDirection: 'row-reverse',
              alignItems: 'center',
              gap: '.5em',
              cursor: 'pointer',
            }}
          >
            <Settings
              color="colorPrimer"
              sx={{
                fontSize: '1.5em !important',
              }}
            />
            {FindLangKey(
              'personalization-drawer-navbar-title'
            )}
          </span>

          <UserGuidFetchButton />
          <span
            onClick={() => navigate('/about')}
            style={{
              display: 'flex',
              flexDirection: 'row-reverse',
              alignItems: 'center',
              gap: '.5em',
              cursor: 'pointer',
            }}
          >
            <Info
              color="colorPrimer"
              sx={{
                fontSize: '1.5em !important',
              }}
            />
            {about_us}
          </span>
          <span
            onClick={() => navigate('/ContactUs')}
            style={{
              display: 'flex',
              flexDirection: 'row-reverse',
              alignItems: 'center',
              gap: '.5em',
              cursor: 'pointer',
            }}
          >
            <ContactEmergency
              color="colorPrimer"
              sx={{
                fontSize: '1.5em !important',
              }}
            />
            {contact_us}
          </span>
          <LogOutButton setDrawerOpen={setDrawerOpen} />
        </Stack>
      </div>
    </React.Fragment>
  );
};

export default MainMenuDrawerOptions;
