import { Post } from 'Xhr/Xhr';
import { env } from 'env';

export const GetCarouselImage = async () => {
  return Post(
    'POST',
    `${process.env.REACT_APP_API_URL_API_GET_AD_LIST_IMAGE}`,
    false,
    {
      MobileOrPc: true,
      OutOrInLogin: true,
      PatientOrPerson: false,
    }
  );
};
