import { Container, Stack } from '@mui/material';
import { GlobalContext } from 'Global/Context/GlobalContextProvider';
import React, { useState } from 'react';
import { useContext } from 'react';
import MainMenu from './Components/MainMenu/MainMenu';
import MainMenuDrawer from './Components/MainMenuDrawer/MainMenuDrawer';
import MainTopProfileSummary from './Components/MainTopProfileSummary/MainTopProfileSummary';
import useCheckScreenWidth from 'Global/Hooks/useCheckScreenWidth';
import { useAtom } from 'jotai';
import { InterfaceAtom } from './InterfaceAtom';
import { Outlet } from 'react-router-dom';
import { getPrefrenceTrigger } from 'Global/shared/get.prefrence.trigger.atom';
import Cookies from 'js-cookie';

const UserInterface = ({ setRefreshForImage }) => {
  const screenWidth = useCheckScreenWidth(); // 1024 for Desktop

  const [refreshInterface, setRefreshInterface] =
    useAtom(InterfaceAtom);
  const [keepPadding, setKeepPadding] = useState(false);
  const GlobalConstants = useContext(GlobalContext);
  const IsDrawerOpen = GlobalConstants.IsDrawerOpen;
  const setDrawerOpen = GlobalConstants.setDrawerOpen;

  const MoreRows = localStorage.getItem('MoreRows');
  const [, setTrigger] = useAtom(getPrefrenceTrigger);
  const userCookie = Cookies.get('user');
  const userAccessRole =
    userCookie && JSON.parse(userCookie)?.AccessRole;
  React.useEffect(() => {
    localStorage.removeItem('pay_redirect');

    return () => {
      sessionStorage.removeItem('storedServices');
      sessionStorage.removeItem('previousSearchDate');
    };
  }, []);

  React.useEffect(() => {
    const rows = JSON.parse(MoreRows);
    setKeepPadding(rows);
  }, [MoreRows]);

  // console.log('userAccessRole->', userAccessRole);

  return (
    <React.Fragment>
      <div
        className="menu-layout-color"
        style={{ paddingInline: keepPadding ? '.3em' : '' }}
      >
        {refreshInterface ? (
          <>
            <Stack spacing={6}>
              <div
                style={{
                  marginRight:
                    screenWidth > 1024
                      ? userAccessRole.includes(
                          'Patient'
                        ) && userAccessRole?.length === 1
                        ? '12em'
                        : '20em'
                      : '',
                }}
              >
                {screenWidth > 1024 ? <Outlet /> : <></>}
              </div>
              <MainTopProfileSummary
                setDrawerOpen={setDrawerOpen}
                setRefreshForImage={setRefreshForImage}
                screenWidth={screenWidth}
              />
              <MainMenu />
            </Stack>

            <MainMenuDrawer
              IsDrawerOpen={IsDrawerOpen}
              setDrawerOpen={setDrawerOpen}
              setRefreshInterface={setRefreshInterface}
              setRefreshForImage={setRefreshForImage}
            />
          </>
        ) : (
          <>
            <Stack spacing={6}>
              <div
                style={{
                  marginRight:
                    screenWidth > 1024
                      ? userAccessRole.includes(
                          'Patient'
                        ) && userAccessRole?.length === 1
                        ? '12em'
                        : '20em'
                      : '',
                }}
              >
                {screenWidth > 1024 ? <Outlet /> : <></>}
              </div>
              <MainTopProfileSummary
                setDrawerOpen={setDrawerOpen}
                screenWidth={screenWidth}
              />
              <MainMenu />
            </Stack>

            <MainMenuDrawer
              IsDrawerOpen={IsDrawerOpen}
              setDrawerOpen={setDrawerOpen}
              setRefreshInterface={setRefreshInterface}
              setRefreshForImage={setRefreshForImage}
            />
          </>
        )}
      </div>
    </React.Fragment>
  );
};

export default UserInterface;
