import {
  AppBar,
  Container,
  IconButton,
  Stack,
  Toolbar,
  Box,
} from '@mui/material';
import React, { useContext } from 'react';
import { Menu, Search } from '@mui/icons-material';
import DesktopSideBar from './DesktopSideBar';
import AppBarCustom from 'Global/Components/CommonComponents/AppBarCustom/AppBarCustom';
import { GlobalContext } from 'Global/Context/GlobalContextProvider';
import { Outlet } from 'react-router-dom';
import i18next from 'i18next';

import MenuLinksCreator from 'Global/Components/Generators/MenuLinksCreator/MenuLinksCreator';
import OptionsDrawer from 'Global/Components/CommonComponents/BottomBar/Components/OptionsDrawer';
import { DesktopLinks } from './DesktopLinks';
import { themeColors } from 'Global/JsxThemeColor/JsxThemeColor';
import PersianSupportTextField from 'Global/Components/UnEditableComponents/PersianSupportTextField/PersianSupportTextField';
import FindLangKey from 'Global/Functions/FindLangKey/FindLangKey';
import Cookies from 'js-cookie';

const MainTopProfileSummary = ({
  setDrawerOpen,
  screenWidth,
}) => {
  const HandleOpenDrawer = () => {
    setDrawerOpen((prev) => !prev);
  };
  const rootPaths = useContext(GlobalContext)?.rootPaths;

  const IsDrawerOpen =
    useContext(GlobalContext)?.settingDrawerOpen;

  const setSettingDrawerOpen =
    useContext(GlobalContext)?.setSettingDrawerOpen;

  const lnks = DesktopLinks();

  // const searh_label = FindLangKey('general-public-search');

  const userCookie = Cookies.get('user');
  const userAccessRole =
    userCookie && JSON.parse(userCookie)?.AccessRole;

  return (
    <Container>
      <Stack
        direction="row"
        alignItems="center"
        marginTop="1em"
        justifyContent="space-between"
        paddingX={{ lg: '7.8em' }}
      >
        <AppBar
          color="colorPrimer"
          sx={{
            width: screenWidth > 1024 ? '15%' : '100%',
            height: screenWidth > 1024 ? '100%' : 'initial',
          }}
        >
          <Toolbar
            variant="dense"
            sx={{
              minHeight: '2.3em !important',
              flexDirection:
                screenWidth > 1024
                  ? 'column'
                  : 'row-reverse',
              height:
                screenWidth > 1024 ? '100%' : 'initial',
              paddingInline:
                screenWidth > 1024
                  ? '0 !important'
                  : 'initial',
              justifyContent: 'space-between',
            }}
          >
            {screenWidth < 1024 ? (
              <>
                <IconButton
                  sx={{ color: themeColors.ColorSecondary }}
                  aria-label="menu"
                  onClick={HandleOpenDrawer}
                >
                  <Menu
                    sx={{
                      mr: '.39em',
                      cursor: 'pointer',
                      fill: `${themeColors.ColorSecondary} !important`,
                    }}
                  />
                </IconButton>
              </>
            ) : (
              <Stack>
                <DesktopSideBar />

                {userAccessRole.includes('Patient') &&
                userAccessRole?.length === 1 ? (
                  <></>
                ) : (
                  <AppBarCustom
                    styles={{
                      width: '7em',
                      backgroundColor: 'white',
                      marginRight: '15%',
                      '& *': {
                        color: 'black !important',
                      },
                      overflowY: 'auto',
                    }}
                    screenWidth={screenWidth}
                  >
                    <MenuLinksCreator
                      Links={lnks}
                      AccessRoles={rootPaths}
                    />
                  </AppBarCustom>
                )}

                <OptionsDrawer
                  IsDrawerOpen={IsDrawerOpen}
                  setDrawerOpen={setSettingDrawerOpen}
                />
              </Stack>
            )}
            <>
              {screenWidth < 1024 ? (
                <IconButton
                  sx={{
                    color: themeColors.ColorSecondary,
                  }}
                  aria-label="menu"
                  onClick={HandleOpenDrawer}
                >
                  <Search
                    sx={{
                      mr: '.39em',
                      cursor: 'pointer',
                      fill: `${themeColors.ColorSecondary} !important`,
                    }}
                  />
                </IconButton>
              ) : (
                <></>
              )}
            </>
          </Toolbar>
        </AppBar>
      </Stack>
    </Container>
  );
};

export default MainTopProfileSummary;

