import React from "react";
import { GetUserManual } from "Routes/Protected/MainPage/Adapters/GetUserManual";
import FindLangKey from "Global/Functions/FindLangKey/FindLangKey";
import { Button } from "@mui/material";
import { HelpCenter } from "@mui/icons-material";
const UserGuidFetchButton = () => {
  const downloadingManualMsg = FindLangKey(
    "general-public-alert-downloading-manual"
  );
  return (
    <React.Fragment>
      <span
        onClick={() => {
          GetUserManual(downloadingManualMsg);
        }}
        style={{
          display: "flex",
          flexDirection: "row-reverse",
          alignItems: "center",
          gap: ".5em",
          cursor: "pointer",
        }}
      >
        <HelpCenter
          sx={{
            fontSize: "1.5em !important",
          }}
          color="colorPrimer"
        />
        {FindLangKey("main-menu-drawer-link-manual-download")}
      </span>
    </React.Fragment>
  );
};

export default UserGuidFetchButton;
