import React from 'react';
import { Card } from '@mui/material';
import { ArabicToPersian } from 'utils/ConvertToPersianTextFromArabic';
import { useNavigate } from 'react-router-dom';
import { pageAdAtom } from 'Global/shared/adv.page.atom';
import { useAtom } from 'jotai';
import FrameCard from 'Global/Components/CommonComponents/FrameCard/FrameCard';

const NonCarousel = ({ images }) => {
  const navigate = useNavigate();

  console.log('carousel tests +=>', images);

  return (
    <React.Fragment>
      <div
        style={{
          marginInline: '5%',
          // marginTop: '-10em',
        }}
      >
        {images?.map((image, index) => (
          <FrameCard
            key={image.Id}
            cardProps={{
              borderRadius: '.3em',
              marginBlock: '1em',
              padding: '.3em',
              borderRadius: '20px',
            }}
            jsx={
              <div
                style={{
                  backgroundColor: 'white',
                  borderRadius: '20px',
                }}
                onClick={() => {
                  // sessionStorage.setItem(
                  //   'advImages',
                  //   JSON.stringify(images)
                  // );
                  // navigate('/adv');
                  window.open(image?.linkAddress, '_blank');

                  // window.location.href = image?.linkAddress;
                }}
              >
                <img
                  src={image?.imageList[0]?.Base64Image}
                  alt={image?.Description}
                  style={{
                    width: '100%',
                    height: '11.75em',
                    borderRadius: '20px 20px 0 0',
                  }}
                />
                <p
                  style={{
                    textAlign: 'center',
                  }}
                >
                  {ArabicToPersian(image?.Header)}
                </p>
              </div>
            }
          />
        ))}
      </div>
      <div style={{ paddingBlock: '1em' }}></div>
    </React.Fragment>
  );
};

export default NonCarousel;

