import SwalAlert from 'Global/Components/CommonComponents/SwalAlert/SwalAlert';
import Cookies from 'js-cookie';
import { Post } from 'Xhr/Xhr';
import { env } from 'env';

export const UploadUserImage = async (
  base64,
  setRefreshInterface,
  GlobalConstants,
  operationDoneMsg,
  setRefreshForImage,
  operatingMsg
) => {
  SwalAlert('info', operatingMsg, false);
  const IdPerson =
    Cookies.get('user') && JSON.parse(Cookies.get('user'));
  return Post(
    'POST',
    `${process.env.REACT_APP_API_URL_API_UPLOAD_USER_IMAGE}`,
    true,
    {
      idUser: parseInt(IdPerson.Id),
      base64Image: base64,
    }
  )
    .then((r) => {
      if (r.data.resCode === 1) {
        localStorage.removeItem('userImage');
        localStorage.setItem(
          'userImage',
          JSON.stringify(base64)
        );
        SwalAlert('success', operationDoneMsg, false).then(
          () => {
            setRefreshInterface((prev) => !prev);
            setRefreshForImage((prev) => !prev);
            location.reload();
          }
        );
      } else {
        SwalAlert('error', r.data.resMessage, false);
      }
    })

    .catch((err) => console.error(err));
};
