import React, { useContext } from "react";
import { Avatar, Container, Stack } from "@mui/material";
import { themeColors } from "Global/JsxThemeColor/JsxThemeColor";
import { useAtom } from "jotai";
import sideBarIconStyles from "./sidebar.icons.colors.module.scss";
import MainMenuDrawerOptions from "../MainMenuDrawer/components/MainMenuDrawerOptions";
import { InterfaceAtom } from "../../InterfaceAtom";
import { GlobalContext } from "Global/Context/GlobalContextProvider";
import { ImageAtom } from "../../ImageAtom";
import { AccountCircle } from "@mui/icons-material";

const DesktopSideBar = () => {
  const imgHeader = "data:image/svg+xml;base64,";

  const userImage =
    localStorage.getItem("userImage") !== null &&
    JSON.parse(localStorage.getItem("userImage"));

  const cachedIcons = JSON.parse(localStorage.getItem("icons"));

  const [, setRefreshInterface] = useAtom(InterfaceAtom);

  const [, setRefreshForImage] = useAtom(ImageAtom);

  const GlobalConstants = useContext(GlobalContext)?.setDrawerOpen;

  const setDrawerOpen = GlobalConstants.setDrawerOpen;

  console.log('userImage->',userImage)

  return (
    <React.Fragment>
      <Container sx={{ position: "relative", overflowX: "hidden !important" }}>
        <Stack sx={{ mt: "1em" }} alignItems="center">
        {userImage?.length > 0 ? (
            <img
              src={`data:image/*;base64,${userImage}`}
              alt=" "
              loading="..."
              width={60}
              height={60}
              style={{ borderRadius: '.3em' }}
            />
          ) : (
            <AccountCircle
              sx={{
                width: '6em',
                height: '6em',
              }}
            />
          )}
          <br/>
          <div className={sideBarIconStyles.container}>
            <MainMenuDrawerOptions
              setDrawerOpen={setDrawerOpen}
              setRefreshInterface={setRefreshInterface}
              setRefreshForImage={setRefreshForImage}
            />
          </div>
        </Stack>
      </Container>
    </React.Fragment>
  );
};

export default DesktopSideBar;
